import React from 'react'
import ReactDOM from 'react-dom'

import { LanguageProvider } from './contexts/language'
import { ThemeProvider } from './contexts/theme'
import App from './App'

import './index.css'
import 'devicon/devicon.min.css'

ReactDOM.render(
  <React.StrictMode>
    <LanguageProvider>
      <ThemeProvider>
        <App />
      </ThemeProvider>
    </LanguageProvider>
  </React.StrictMode>,
  document.getElementById('root')
)

