import React from 'react'
import PropTypes, { type InferProps } from 'prop-types'
import uniqid from 'uniqid'

import GitHubIcon from '@mui/icons-material/GitHub'
import LaunchIcon from '@mui/icons-material/Launch'

import { type TechStack, techStackIcon } from '../../../utils/Portfolio'

import './ProjectItem.css'

const ProjectItemPropTypes = {
  project: PropTypes.shape({
    name: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    stack: PropTypes.arrayOf(PropTypes.string),
    sourceCode: PropTypes.string,
    livePreview: PropTypes.string
  }).isRequired
}

type ProjectItemTypes = InferProps<typeof ProjectItemPropTypes>

const ProjectItem: React.FC<ProjectItemTypes> = ({ project }) => (
  <div className="project">
    <h3>{project.name}</h3>

    <p className="project__description">{project.description}</p>
    {project.stack != null && (
      <ul className="project__stack">
        {project.stack.map((item) => (
          <li key={uniqid()} className="project__stack-item">
            <i className={techStackIcon(item as TechStack)}></i> {item}
          </li>
        ))}
      </ul>
    )}

    {project.sourceCode && (
      <a
        href={project.sourceCode}
        target="_blank"
        aria-label="source code"
        className="link link--icon"
        rel="noreferrer"
      >
        <GitHubIcon />
      </a>
    )}

    {project.livePreview && (
      <a
        href={project.livePreview}
        target="_blank"
        rel="noreferrer"
        aria-label="live preview"
        className="link link--icon"
      >
        <LaunchIcon />
      </a>
    )}
  </div>
)

ProjectItem.propTypes = ProjectItemPropTypes
export default ProjectItem
