import React from 'react'
import PropTypes, { type InferProps } from 'prop-types'

import './CertificationItem.css'

const ProjectItemPropTypes = {
  certification: PropTypes.shape({
    name: PropTypes.string.isRequired,
    credentialUrl: PropTypes.string,
    badge: PropTypes.string
  }).isRequired
}

type ProjectItemTypes = InferProps<typeof ProjectItemPropTypes>

const CertificationItem: React.FC<ProjectItemTypes> = ({ certification }) => (
  <li className="certification">
    <a
      href={certification.credentialUrl ?? '#'}
      target="_blank"
      rel="noreferrer"
      aria-label="credentialUYrl"
      className="certification__link"
    >
      {certification.badge ? (
        <div className="certification__badge">
          <img
            src={certification.badge}
            alt={certification.name}
            loading="lazy"
          />
        </div>
      ) : null}
      <p className="certification__name">{certification.name}</p>
    </a>
  </li>
)

CertificationItem.propTypes = ProjectItemPropTypes
export default CertificationItem
