import React, { useContext } from 'react'
import uniqid from 'uniqid'

import { LanguageContext } from '../../contexts/language'

import CertificationItem from './CertificationItem/CertificationItem'

import './Certifications.css'

const Certifications = (): JSX.Element | null => {
  const { dictionary, portfolio } = useContext(LanguageContext)
  const { certificates } = portfolio
  return (
    <section id="certifications" className="section certifications">
      <h2 className="section__title">{dictionary.certifications}</h2>
      <ul className="certifications__grid">
        {certificates
          .filter((c) => !!c.badge)
          .map((certification) => (
            <CertificationItem key={uniqid()} certification={certification} />
          ))}
      </ul>
    </section>
  )
}

export default Certifications
