import { header } from '../../utils/Portfolio'
import Navbar from '../Navbar/Navbar'

import './Header.css'

const Header = (): JSX.Element => {
  const { homepage, title } = header

  return (
    <header className="header center">
      <h3>
        {homepage ? (
          <a href={homepage} className="link">
            {title}
          </a>
        ) : (
          title
        )}
      </h3>
      <Navbar />
    </header>
  )
}

export default Header
