import { useContext } from 'react'
import uniqid from 'uniqid'

import { LanguageContext } from '../../contexts/language'

import EducationItem from './EducationItem/EducationItem'

import './Education.css'

const Education = (): JSX.Element | null => {
  const { dictionary, portfolio } = useContext(LanguageContext)
  const { education } = portfolio
  if (education.length === 0) return null
  return (
    <section id="education" className="section education">
      <h2 className="section__title">{dictionary.education}</h2>
      <ul className="education__grid">
        {education.map((grade) => (
          <EducationItem key={uniqid()} grade={grade} showLogo={true} />
        ))}
      </ul>
    </section>
  )
}

export default Education
