import React from 'react'
import PropTypes, { type InferProps } from 'prop-types'
import uniqid from 'uniqid'

import Avatar from '@mui/material/Avatar'

import { type TechStack, techStackIcon } from '../../../utils/Portfolio'
import ExperienceProjectItem from '../ExperienceProjectItem/ExperienceProjectItem'

import './ExperienceItem.css'

const ExperienceItemPropTypes = {
  work: PropTypes.shape({
    institution: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    date: PropTypes.string.isRequired,
    description: PropTypes.arrayOf(PropTypes.string),
    technologies: PropTypes.arrayOf(PropTypes.string),
    logo: PropTypes.string,
    projects: PropTypes.arrayOf(
      PropTypes.shape({
        institution: PropTypes.string.isRequired,
        name: PropTypes.string.isRequired,
        date: PropTypes.string.isRequired,
        description: PropTypes.arrayOf(PropTypes.string),
        technologies: PropTypes.arrayOf(PropTypes.string),
        logo: PropTypes.string,
        location: PropTypes.string
      }).isRequired
    )
  }).isRequired
}

type ExperienceItemTypes = InferProps<typeof ExperienceItemPropTypes>

const ExperienceItem: React.FC<ExperienceItemTypes> = ({ work }) => {
  return (
    <li className="work">
      <div className="work-mask">
        {work?.logo ? (
          <Avatar
            variant="rounded"
            src={work.logo}
            alt={work.institution}
            className="work__logo"
            imgProps={{ style: { objectFit: 'contain', borderRadius: '20%' } }}
            sx={{ width: '4rem', height: '4rem' }}
          ></Avatar>
        ) : null}
        <h3 className="work__institution">{work.institution}</h3>
        <p className="work__title">{work.title}</p>
        <p className="work__date">{work.date}</p>
        {work.description != null ? (
          <ul className="work_description__grid">
            {work.description.map((detail) => (
              <li key={uniqid()} className="work__description work__detail">
                {detail}
              </li>
            ))}
          </ul>
        ) : null}
        {work.projects != null ? (
          <ul className="experience_projects__grid">
            {work.projects.map((project) => (
              <ExperienceProjectItem key={uniqid()} project={project} />
            ))}
          </ul>
        ) : null}
        {work.technologies != null ? (
          <ul className="work__technologies">
            {work.technologies.map((item) => (
              <li key={uniqid()} className="work__technologies-item">
                <i className={techStackIcon(item as TechStack)}></i> {item}
              </li>
            ))}
          </ul>
        ) : null}
      </div>
    </li>
  )
}

ExperienceItem.propTypes = ExperienceItemPropTypes
export default ExperienceItem
