import { useContext, useState } from 'react'

import Brightness2Icon from '@mui/icons-material/Brightness2'
import CloseIcon from '@mui/icons-material/Close'
import MenuIcon from '@mui/icons-material/Menu'
import WbSunnyRoundedIcon from '@mui/icons-material/WbSunnyRounded'
import { FormControl, MenuItem, Select } from '@mui/material'
import useMediaQuery from '@mui/material/useMediaQuery'

import { LanguageContext } from '../../contexts/language'
import { ThemeContext } from '../../contexts/theme'
import { type LanguageList } from '../../services/constants'
import { LanguageOptions } from '../../services/constants/languages'

import './Navbar.css'

const Navbar = (): JSX.Element => {
  const { themeName, toggleTheme } = useContext(ThemeContext)
  const { userLanguage, dictionary, portfolio, userLanguageChange } =
    useContext(LanguageContext)
  const { contact, education, experience, projects, skills } = portfolio
  const [showNavList, setShowNavList] = useState(false)

  const breakpointXS = useMediaQuery('(max-width:600px)')
  const toggleNavList = (): void => {
    setShowNavList(!showNavList)
  }

  return (
    <nav className="center nav">
      <ul
        style={{ display: showNavList ? 'flex' : undefined }}
        className="nav__list"
      >
        {education.length > 0 ? (
          <li className="nav__list-item">
            <a
              href="#education"
              onClick={toggleNavList}
              className="link link--nav"
            >
              {dictionary.education}
            </a>
          </li>
        ) : null}

        {experience.length > 0 ? (
          <li className="nav__list-item">
            <a
              href="#experience"
              onClick={toggleNavList}
              className="link link--nav"
            >
              {dictionary.experience}
            </a>
          </li>
        ) : null}

        {projects.length > 0 ? (
          <li className="nav__list-item">
            <a
              href="#projects"
              onClick={toggleNavList}
              className="link link--nav"
            >
              {dictionary.projects}
            </a>
          </li>
        ) : null}

        <li className="nav__list-item">
          <a
            href="#certifications"
            onClick={toggleNavList}
            className="link link--nav"
          >
            {dictionary.certifications}
          </a>
        </li>

        {skills.length > 0 ? (
          <li className="nav__list-item">
            <a
              href="#skills"
              onClick={toggleNavList}
              className="link link--nav"
            >
              {dictionary.skills}
            </a>
          </li>
        ) : null}

        {contact.email ? (
          <li className="nav__list-item">
            <a
              href="#contact"
              onClick={toggleNavList}
              className="link link--nav"
            >
              {dictionary.contact}
            </a>
          </li>
        ) : null}
      </ul>

      <FormControl sx={{ m: 1, minWidth: 50 }} size="small" variant="standard">
        <Select
          className="select-language"
          value={userLanguage}
          onChange={(e) => {
            userLanguageChange(e.target.value as LanguageList)
          }}
        >
          {Object.entries(LanguageOptions).map(([id, language]) => (
            <MenuItem key={id} value={id}>
              {language.flag}&nbsp; {!breakpointXS && language.name}
            </MenuItem>
          ))}
        </Select>
      </FormControl>

      <button
        type="button"
        onClick={toggleTheme}
        className="btn btn--icon nav__theme"
        aria-label="toggle theme"
      >
        {themeName === 'dark' ? <WbSunnyRoundedIcon /> : <Brightness2Icon />}
      </button>

      <button
        type="button"
        onClick={toggleNavList}
        className="btn btn--icon nav__hamburger"
        aria-label="toggle navigation"
      >
        {showNavList ? <CloseIcon /> : <MenuIcon />}
      </button>
    </nav>
  )
}

export default Navbar
