import { useContext } from 'react'
import uniqid from 'uniqid'

import { LanguageContext } from '../../contexts/language'
import { type TechStack, techStackIcon } from '../../utils/Portfolio'

import './Skills.css'

const Skills = (): JSX.Element | null => {
  const { dictionary, portfolio } = useContext(LanguageContext)
  const {
    programmingLanguages,
    backend,
    frontend,
    database,
    devOps,
    languages
  } = portfolio

  return (
    <section className="section skills" id="skills">
      <h2 className="section__title">{dictionary.skills}</h2>
      <div className="skills__grid">
        <div>
          <h3 className="skills__title">{dictionary.programmingLanguages}</h3>
          <ul className="skills__list">
            {programmingLanguages.map((skill) => (
              <li key={uniqid()} className="skills__list-item btn btn--plain">
                <i className={techStackIcon(skill as TechStack)}></i> {skill}
              </li>
            ))}
          </ul>
        </div>
        <div>
          <h3 className="skills__title">{dictionary.backEnd}</h3>
          <ul className="skills__list">
            {backend.map((skill) => (
              <li key={uniqid()} className="skills__list-item btn btn--plain">
                <i className={techStackIcon(skill as TechStack)}></i> {skill}
              </li>
            ))}
          </ul>
        </div>
        <div>
          <h3 className="skills__title">{dictionary.frontEnd}</h3>
          <ul className="skills__list">
            {frontend.map((skill) => (
              <li key={uniqid()} className="skills__list-item btn btn--plain">
                <i className={techStackIcon(skill as TechStack)}></i> {skill}
              </li>
            ))}
          </ul>
        </div>
        <div>
          <h3 className="skills__title">{dictionary.database}</h3>
          <ul className="skills__list">
            {database.map((skill) => (
              <li key={uniqid()} className="skills__list-item btn btn--plain">
                <i className={techStackIcon(skill as TechStack)}></i> {skill}
              </li>
            ))}
          </ul>
        </div>
        <div>
          <h3 className="skills__title">{dictionary.devOps}</h3>
          <ul className="skills__list">
            {devOps.map((skill) => (
              <li key={uniqid()} className="skills__list-item btn btn--plain">
                <i className={techStackIcon(skill as TechStack)}></i> {skill}
              </li>
            ))}
          </ul>
        </div>
        <div>
          <h3 className="skills__title">{dictionary.languages}</h3>
          <ul className="skills__list">
            {languages.map((skill) => (
              <li key={uniqid()} className="skills__list-item btn btn--plain">
                {skill.name} ({skill.level})
              </li>
            ))}
          </ul>
        </div>
      </div>
      {/* <ul className="skills__list">
        {skills.map((skill) => (
          <li key={uniqid()} className="skills__list-item btn btn--plain">
            {skill}
          </li>
        ))}
      </ul> */}
    </section>
  )
}

export default Skills
