import React, { useContext } from 'react'
import PropTypes, { type InferProps } from 'prop-types'
import uniqid from 'uniqid'

import { LanguageContext } from '../../../contexts/language'

import './ExperienceProjectItem.css'

const ExperienceProjectItemPropTypes = {
  project: PropTypes.shape({
    institution: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    date: PropTypes.string.isRequired,
    description: PropTypes.arrayOf(PropTypes.string),
    technologies: PropTypes.arrayOf(PropTypes.string),
    logo: PropTypes.string,
    location: PropTypes.string
  }).isRequired
}

type ExperienceProjectItemTypes = InferProps<
  typeof ExperienceProjectItemPropTypes
>

const ExperienceProjectItem: React.FC<ExperienceProjectItemTypes> = ({
  project
}) => {
  const { dictionary } = useContext(LanguageContext)
  return (
    <li className="work_project">
      <p className="work_project__name text-justify">
        <span style={{ fontWeight: 'bold' }}>{dictionary.project}:</span>{' '}
        {project.institution} - {project.name}
      </p>
      {project.description != null ? (
        <ul className="work_project_description__grid">
          {project.description.map((detail) => (
            <li
              key={uniqid()}
              className="work_project__description work_project__detail"
            >
              {detail}
            </li>
          ))}
        </ul>
      ) : null}
      {project.technologies != null ? (
        <ul className="work_project__technologies">
          {project.technologies.map((item) => (
            <li key={uniqid()} className="work_project__technologies-item">
              {item}
            </li>
          ))}
        </ul>
      ) : null}
    </li>
  )
}

ExperienceProjectItem.propTypes = ExperienceProjectItemPropTypes
export default ExperienceProjectItem
