import { useContext, useRef } from 'react'
import Html2Pdf from 'js-html2pdf'
import ReactToPrint, { useReactToPrint } from 'react-to-print'

import GitHubIcon from '@mui/icons-material/GitHub'
import LinkedInIcon from '@mui/icons-material/LinkedIn'
import Avatar from '@mui/material/Avatar'

import profileDark from '../../assets/images/profile-dark.jpg'
import profileLight from '../../assets/images/profile-light.jpg'
import { LanguageContext } from '../../contexts/language'
import { ThemeContext } from '../../contexts/theme'
import { ThemeName } from '../../services/constants'
import CvOneSideV2 from '../CvOneSideV2/CvOneSideV2'

import './About.css'

const About = (): JSX.Element => {
  const { themeName } = useContext(ThemeContext)
  const { dictionary, portfolio } = useContext(LanguageContext)
  const { name, role, aboutMe, social } = portfolio.about
  const componentRef = useRef<HTMLDivElement>(null)

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const handlePrint = useReactToPrint({
    content: () => componentRef.current,
    onPrintError: (error) => {
      console.log(error)
    },
    print: async (printIframe) => {
      console.log(printIframe)
      const document = printIframe.contentDocument
      if (document == null) return
      const ticketElement = document.getElementsByClassName('cv_one_side')[0]
      // ticketElement.style.display = 'block'
      const options = {
        // margin: [10, 5, 10, 5],
        margin: 10,
        filename: `Piero Santisteban - ${dictionary.resume}.pdf`,
        // image: { type: 'jpeg', quality: 1 },
        // jsPDF: { unit: 'px', format: [600, 800], orientation: 'portrait' }
        jsPDF: { unit: 'mm', format: 'a4', orientation: 'portrait' }
      }
      const exporter = new Html2Pdf(ticketElement, options)
      await exporter.getPdf(options)
    }
  })

  return (
    <div className="about center">
      <Avatar
        alt="Piero Santisteban"
        src={themeName === ThemeName.DARK ? profileDark : profileLight}
        className="about__avatar"
      />

      {Boolean(name) && (
        <h1 className="about__hi">
          {dictionary.hi}, {dictionary.im}{' '}
          <span className="about__name">{name}.</span>
        </h1>
      )}

      {Boolean(role) && <h2 className="about__role">{role}</h2>}
      <p className="about__desc">{aboutMe ?? ''}</p>

      <div className="about__contact center">
        <div>
          <ReactToPrint
            trigger={() => (
              <a href="#" download>
                <span className="btn btn--outline">{dictionary.resume} 📝</span>
              </a>
            )}
            content={() => componentRef.current}
            documentTitle="Piero Santisteban - Resume"
          />
          {/* <button onClick={handlePrint}>
            <span className="btn btn--outline">{dictionary.resume}</span>
          </button> */}
          <div style={{ display: 'none' }}>
            <CvOneSideV2 ref={componentRef} />
          </div>
        </div>
        {Boolean(social) && (
          <>
            {Boolean(social.github) && (
              <a
                href={social.github}
                target="_blank"
                rel="noreferrer"
                aria-label="github"
                className="link link--icon"
              >
                <GitHubIcon />
              </a>
            )}

            {Boolean(social.linkedin) && (
              <a
                href={social.linkedin}
                target="_blank"
                rel="noreferrer"
                aria-label="linkedin"
                className="link link--icon"
              >
                <LinkedInIcon />
              </a>
            )}
          </>
        )}
      </div>
    </div>
  )
}

export default About
