import React from 'react'
import PropTypes, { type InferProps } from 'prop-types'

import Avatar from '@mui/material/Avatar'

import './EducationItem.css'

const EducationItemPropTypes = {
  grade: PropTypes.shape({
    institution: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    date: PropTypes.string.isRequired,
    logo: PropTypes.string
  }).isRequired,
  showLogo: PropTypes.bool
}

type EducationItemTypes = InferProps<typeof EducationItemPropTypes>

const EducationItem: React.FC<EducationItemTypes> = ({ grade, showLogo }) => {
  return (
    <li className="grade">
      {showLogo && grade?.logo ? (
        <Avatar
          variant="square"
          src={grade.logo}
          alt={grade.institution}
          className="grade__logo"
          sx={{ width: '4rem', height: '4rem' }}
        ></Avatar>
      ) : null}
      <h3 className="grade__institution">{grade.institution}</h3>
      <p className="grade__title">{grade.title}</p>
      <p className="grade__date">{grade.date}</p>
    </li>
  )
}

EducationItem.propTypes = EducationItemPropTypes

export default EducationItem
